const DynamicTable = ({header,data}) => {

  const renderItems = () => {
    return data.map((item,index) => (
      
      <tr key = {index} class="bg-white border-b dark:bg-gray-800 dark:border-gray-700">
        {Object.values(item).map((value,index) => (
            <td key={index}  class="w-1/2 text-center" >{value}</td>
            )
          )
        }
      </tr>
    )
    )  
  }

  return (
    <div class="bg-white relative w-[33%] h-full overflow-y-auto shadow-md sm:rounded-lg">
      <table class="w-full text-xl text-left text-gray-500 dark:text-gray-400">
        <thead class="text-lg text-gray-700 uppercase bg-gray-100 dark:bg-gray-700 dark:text-gray-400">
          <tr>
            <th class="w-1/2 text-center">{header}</th>
            <th class="w-1/2 text-center">Amount</th>
          </tr>
        </thead>
        <tbody>
          {renderItems()}
        </tbody>
    </table>
  </div>
  )
  
  
}





